.page-body {
  font-family: "PT Mono Regular", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  color: #666666;
  height: 100%;
}

.center-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

.center-container--fixed {
  position: fixed;
}

.page-header {
  min-width: 100%;
  margin: 0;
  padding: 33px 50px 0 50px;
  background-color: #1b2d37;
}
@media (max-width: 1023px) {
  .page-header {
    padding: 33px 25px 0 25px;
  }
}
@media (max-width: 767px) {
  .page-header {
    padding: 33px 15px 0 15px;
  }
}

.page-header__inner {
  display: flex;
  justify-content: space-between;
  max-width: 1420px;
  margin: 0 auto;
  padding: 0;
}

.page-header__logo-block {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
}

.page-header__logo-block img {
  margin: 0 16px 0 0;
  padding: 0;
}
@media (max-width: 767px) {
  .page-header__logo-block img {
    width: 38px;
    height: 38px;
  }
}

@media (max-width: 767px) {
  .page-header__logo-text-block {
    display: none;
  }
}

.page-header__logo-text-block h3 {
  font-family: "Roboto Condensed Bold", "Arial", sans-serif;
  font-size: 25px;
  line-height: 29px;
  color: white;
  margin: 0 0 12px 0;
  padding: 0;
}

.page-header__logo-text-block p {
  font-family: "Roboto Medium", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  color: #c4c4c4;
  margin: 0;
  padding: 0;
}

.page-header__contacts {
  display: flex;
  justify-content: baseline;
  align-items: center;
  margin: 0;
  padding: 0;
}

.page-header__contacts-mail {
  margin: 0 122px 0 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .page-header__contacts-mail {
    margin: 0;
  }
}

.page-header__contacts-mail a {
  font-family: "PT Mono Regular", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  color: #666666;
  position: relative;
  color: white;
  margin: 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .page-header__contacts-mail a {
    font-size: 1px;
    color: transparent;
  }
}
.page-header__contacts-mail a::before {
  content: "";
  position: absolute;
  top: 0;
  left: -45px;
  background-image: url("../img/desktop/mail-icon.svg");
  background-size: 19px 14px;
  width: 19px;
  height: 14px;
}
@media (max-width: 1023px) {
  .page-header__contacts-mail a::before {
    visibility: visible;
    background-image: url("../img/tablet/mail-icon.svg");
    background-size: 25px 19px;
    width: 25px;
    height: 19px;
    top: -20px;
    left: -75px;
  }
}

.page-header__contacts-phone {
  margin: 0 38px 0 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .page-header__contacts-phone {
    margin: 0;
  }
}

.page-header__contacts-phone a {
  font-family: "PT Mono Regular", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  color: #666666;
  position: relative;
  color: white;
  margin: 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .page-header__contacts-phone a {
    visibility: hidden;
    font-size: 1px;
    color: transparent;
  }
}
.page-header__contacts-phone a::before {
  content: "";
  position: absolute;
  top: 0;
  left: -45px;
  background-image: url("../img/desktop/phone-icon.svg");
  background-size: 18px 18px;
  width: 18px;
  height: 18px;
}
@media (max-width: 1023px) {
  .page-header__contacts-phone a::before {
    visibility: visible;
    background-image: url("../img/tablet/phone-icon.svg");
    background-size: 24px 24px;
    width: 24px;
    height: 24px;
    top: -24px;
    left: -31px;
  }
}

.page-header__contacts button {
  background-color: transparent;
  border: 1px solid white;
  border-radius: 5px;
  outline: 0;
  color: white;
  margin: 0;
  padding: 10px;
}
@media (max-width: 1023px) {
  .page-header__contacts button {
    display: none;
  }
}

.page-main {
  flex: 1;
  min-width: 100%;
  margin: -62px 0 0 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .page-main {
    margin-top: -53px;
  }
}

.page-footer {
  min-width: 100%;
  margin: 0;
  padding: 0;
}

.page-footer__contacts-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 1420px;
  margin: 0 auto;
  padding: 0;
}

.page-footer__logo-block {
  display: flex;
  margin: 0;
  padding: 0;
}

.page-footer__logo-block img {
  margin: 0 16px 54px 0;
  padding: 0;
}

.page-footer__logo-text-block {
  margin: 0;
  padding: 0;
}

.page-footer__logo-text-block h3 {
  font-family: "Roboto Condensed Bold", "Arial", sans-serif;
  font-size: 25px;
  line-height: 29px;
  color: white;
  color: #203c42;
  margin: 0 0 16px 0;
  padding: 0;
}

.page-footer__logo-text-block p {
  font-family: "Roboto Medium", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  color: #c4c4c4;
  margin: 0;
  padding: 0;
}

.page-footer__socials {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.page-footer__socials-facebook {
  position: relative;
  margin: 0 35px 0 0;
  padding: 0;
}
.page-footer__socials-facebook::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-image: url("../img/facebook.svg");
  background-size: 26px 26px;
  width: 26px;
  height: 26px;
}

.page-footer__socials-classmates {
  position: relative;
  margin: 0 35px 0 0;
  padding: 0;
}
.page-footer__socials-classmates::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-image: url("../img/classmates.svg");
  background-size: 26px 26px;
  width: 26px;
  height: 26px;
}

.page-footer__socials-vk {
  position: relative;
  margin: 0;
  padding: 0;
}
.page-footer__socials-vk::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-image: url("../img/vk.svg");
  background-size: 27px 26px;
  width: 27px;
  height: 26px;
}

.page-footer__site-partitions h3 {
  font-family: "Roboto Condensed Regular", "Arial", sans-serif;
  font-size: 22px;
  line-height: 34px;
  color: #00b2ff;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #1b2d37;
  margin: 0 0 29px 0;
  padding: 0;
}

.page-footer__partitions-list {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
  width: 200px;
  height: 114px;
  list-style: none;
  margin: 0;
  padding: 0;
}

.page-footer__copyrights-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1420px;
  margin: 0 auto;
  padding: 0;
}

.page-footer__privacy-policy {
  display: flex;
  margin: 0;
  padding: 0;
}